*,
::before,
::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  /* color */
  --body-bg-color: #ffffff;
  --sidebar-bg-color: #f4f4f4;
  --text-color: #333333;
  --title-color: #111111;
  --date-color: #1f94c2;

  --icon-color: var(--text-color);
  --circle-color: #aaa;
  --circle-color-plain: #555;

  /* --dark-body-bg-color */
  --dark-body-bg-color: #242424;
  --dark-sidebar-bg-color: #171717;
  --dark-text-color: #d8d8d8;
  --dark-title-color: #ffffff;
  --dark-date-color: #e6ef71;

  --dark-icon-color: var(--dark-text-color);
  --dark-circle-color: rgb(108, 108, 108);
  --dark-circle-color-plain: rgb(238, 238, 238);

  /* fonts */
  --body-font: "Poppins", "Sans-serif";
  --h1-font-size: 1.7rem;
  --h2-font-size: 1.3rem;
  --h3-font-size: 1.1rem;
  --h4-font-size: 1rem;

  /* helpers */

  --spacing-1: 0.4rem;
  --spacing-2: 0.8rem;
  --spacing-3: 1.2rem;
  --spacing-4: 1.5rem;
  --spacing-5: 2rem;
  --spacing-6: 4rem;
}

body {
  font-family: "Poppins", "Sans-serif";
  font-family: var(--body-font);
  font-weight: 200;
  color: #333333;
  color: var(--text-color);
  background-color: #ffffff;
  background-color: var(--body-bg-color);
}
.dark {
  color: #d8d8d8;
  color: var(--dark-text-color);
  background-color: #242424;
  background-color: var(--dark-body-bg-color);
}
.dark h1,
.dark h2,
.dark h3 {
  color: #ffffff;
  color: var(--dark-title-color);
}

h1,
h2,
h3 {
  margin-bottom: 0.4rem;
  margin-bottom: var(--spacing-1);
  font-weight: 400;
  color: #111111;
  color: var(--title-color);
}

h1 {
  font-size: 1.7rem;
  font-size: var(--h1-font-size);
  text-transform: uppercase;
}

h2 {
  display: flex;
  align-items: center;
  margin-bottom: 1.5rem;
  margin-bottom: var(--spacing-4);
  font-size: 1.3rem;
  font-size: var(--h2-font-size);
  letter-spacing: 4px;
  text-transform: uppercase;
}

button {
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.dark button {
  color: #d8d8d8;
  color: var(--dark-text-color);
}

a {
  text-decoration: none;
  color: inherit;
}

p {
  margin-bottom: 0.8rem;
  margin-bottom: var(--spacing-2);
}

ul {
  padding-left: 1.3rem;
}

li {
  margin-bottom: 0.8rem;
  margin-bottom: var(--spacing-2);
}
/*  */

.mb3 {
  margin-bottom: 1.2rem;
  margin-bottom: var(--spacing-3);
  margin-right: 2rem;
  margin-right: var(--spacing-5);
}

.mb5 {
  margin-bottom: 2rem;
  margin-bottom: var(--spacing-5);
  margin-top: 2rem;
  margin-top: var(--spacing-5);
  margin-right: 2rem;
  margin-right: var(--spacing-5);
}

/* grid */
.grid__row {
  margin: 1.2rem;
  margin: var(--spacing-3);
}

@media screen and (min-width: 768px) {
  .grid__container {
    display: grid;
    grid-template-columns: 1fr 3fr;
  }
  .grid__row {
    display: grid;
    grid-template-columns: 1fr 2fr;
    margin: 0;
  }
}

/* layout */

.action {
  display: flex;
  justify-content: space-between;
}

.sidebar {
  padding: 1.5rem;
  background-color: #f4f4f4;
  background-color: var(--sidebar-bg-color);
}

.dark .sidebar {
  background-color: #171717;
  background-color: var(--dark-sidebar-bg-color);
}

/* icons */

.circle {
  display: inline-block;
  width: 20px;
  height: 20px;
  margin-right: 8px;
  border-radius: 50%;
  background-color: #aaa;
  background-color: var(--circle-color);
}

.dark .circle {
  background-color: rgb(108, 108, 108);
  background-color: var(--dark-circle-color);
}

.circle-full {
  background-color: #555;
  background-color: var(--circle-color-plain);
}

.dark .circle-full {
  background-color: rgb(238, 238, 238);
  background-color: var(--dark-circle-color-plain);
}

/* main */

.main {
  padding: 1.5rem;
}

/* Generate pdf */

#cv-print {
  width: 100%;
}

.user
{
    text-align: center;

}

.user__avatar
{
    border-radius: 50%;
    max-width: 50%;
}

.user__infos
{
    text-align: left;
    padding: var(--spacing-3) 0 var(--spacing-1) 0;
}

.user__info
{
    display: flex;
}

.user__info svg 
{
    color: var(--icon-color);
    margin-right: 1rem;
}

.dark .user__info svg {
    color:var(--dark-icon-color);
  }
.skills
{
    margin: var(--spacing-3) 0;
}

.skill 
{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.interests
{
    margin-left: 10%;
    margin-right: 20%;
}

.interests svg 
{
    margin: var(--spacing-1);
}
.grid__item
{
    margin-bottom: var(--spacing-5);
}

.grid__date
{
    font-weight: 500;
}

.grid__title
{
    margin-bottom: 0;
}

.grid__location
{
    font-weight: 300;
    color: var(--date-color);
}

.grid__mission
{
    margin-bottom: var(--spacing-1);
}
